*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,body{
  height: 100%;
}

body{
  background-image: url(./wishWall_background.jpg);
  background-repeat: no-repeat;
  background-position:center top;
  background-size: auto 120%;
  background-color: #111;
}

.appHeader{
  font-size: 2.5rem;
  text-align: center;
  padding: 2rem;
  background: #111;
  color: whitesmoke;

  font-style: italic;
  font-family: serif;
}

#headerRiven{
  background-image: url(./emblem_banner_riven.jpg);
}

#headerPower{
  background-image: url(./emblem_banner_power.jpg);
}

/* ///// GRID - WISH WALL ///// */

.content{
  display: block;
  margin: 50px auto;
  width:50%;
}

.wall-container{
  display: grid;
  grid-template-columns:repeat(5, 4fr);
  grid-gap: 20px 20px;

  margin:20px auto;
  width: 75%;
}

.wall-item{
  display: inline-grid;
  margin: 2px;
}

.symbolWrapper{
    position: relative;
    top: 0;
    left: 0;
}

.image{
  display: inline-grid;
  margin: 2px;

  position: relative;
  top:0;
  left:0;

  max-width: 90%;
  max-height: 90%;

  /* border: 4px goldenrod solid; placeholder */ 
  border-radius: 50%;
}

.image:hover{
  cursor: pointer;
}

.ring{
  position: absolute;
  top: -10px;
  left:-6px;
  width: 100%;
  z-index: -1;
}


/* .show{
  display: block;
}

.hide{
  display: none;
} */

.buttonWrapper{
  display: block;
  margin: 0 auto;
  border-radius: 50%;
}

.button{
  border: none;
  margin: 0 auto;
  display: block;

  width: 25%;
  border-radius: 80%;
}

.button:hover{
  cursor: pointer;
}

