.modalMedia{
  display: block;
  width:50%;
  margin:10% auto 2% auto;

  border: 4px  whitesmoke solid;
}

#wish12-modal{
  background-image: url(./traveler.webp);
}

.fontIcon{
  font-size: 2rem;
  text-align: center;
  color: dimgray;
}

.chestWrapper{
  width: calc(1490px * var(--chest-size));
  height: calc(982px * var(--chest-size));
  overflow: hidden;
  position: relative;
  top: 5%;
  margin: 4em auto;
  border: 2px solid whitesmoke;
}

.chest{
    display: block;
    height: calc(11970*var(--chest-size));
    position: absolute;
}

.chest:hover{
  cursor: pointer;
}

.chestOpen{
  animation: chestOpen 1.5s steps(10) forwards;
  -webkit-animation: lastFrame 1.5s steps(9) forwards;
}

@keyframes chestOpen{
 from{
   transform: translate3d(0px,0,0);
 }
 to{
   transform: translate3d(-100%,0,0);
 }
}

@keyframes lastFrame {
    from { transform: translate3d(0px,0,0); }
    to { transform: translate3d(-90%,0,0); }
}


.shuroChi,
.morgeth,
.vault,
.riven{
  display: block;
  margin: 150px auto;
  width: 80%;
  height: 80%;
}

.shuroChi:hover,
.morgeth:hover,
.vault:hover,
.riven:hover{
  cursor: pointer;
}


.play,
.playDrifter{
  display: block;
  width: 4%;
  border-radius: 5px;
  margin: 0 auto;
  padding: 8px;

  border: none;
  background-color: whitesmoke;

  font-size: 2rem;
  text-align: center;
  color: dimgray;
}

.playHope{
  position: absolute;
  left: 47%;
  top: 40%;
  padding: 24px;
  width: auto;
  height: auto;
 
  border: none;
  border-radius: 10px;
  background-color: whitesmoke;

  font-size:3rem;
  text-align: center;
  color: dimgray;
}

:root{
  --pixel-size:1.45;
  --item-size:1.08;
  --chest-size:.8;
}

#wrapper_border,
.shipWrapper{
  position:absolute;
  top:15%;
  right:5%;
  width: calc(180px * var(--pixel-size));
  height: calc(180px * var(--pixel-size));
  overflow: hidden;

  animation: itemDrop 1s;
}

@keyframes itemDrop {
  from{
    top:0;
    opacity: 0;
  }
  to{
    top:15%;
    opacity: 1;
  }
}

.etherialkey,
.ship{
  position:absolute;
  top:30.5%;
  right:30.2%;
  height: calc(96px*var(--item-size));
  height: calc(96px*var(--item-size));
  border: 2px solid whitesmoke;
  z-index: 1;
}

.item,
.shipLoot{
  position:relative;
  width: calc(2896px * var(--pixel-size));
  animation: itemAnimation 1.2s steps(16);
}

@keyframes itemAnimation {
  from{
    transform: translate3d(0px,0,0);
  }
  to{
    transform: translate3d(-100%,0,0);
  }
}


.emblem{
  position: absolute;
  top: 15%;
  right:2%;
  opacity: 1;

  animation-name: emblemAni;
  animation-duration: .5s;
}

@keyframes emblemAni{
  from{
    opacity: 0;
    top: 10%;
  }
  to{
    opacity: 1;
    top:15%;
  }
}

.emblem:before, .emblem:after {
	content: '';
	position: absolute;
	left: -2px;
	top: -2px;
	background: linear-gradient(45deg, #fb0094, rgb(250, 250, 250), #fb0094,
    #fbb800,#fb0094, #fb0094, rgb(250, 250, 250), #fb0094,#9f00fb,#fbb800);
	background-size: 200%;
	width: calc(100% + 4px);
	height: calc(100% + 4px);
	z-index: -1;
	animation: steam 20s linear infinite;
}

@keyframes steam {
	0% {
		background-position: 0 0;
	}
	50% {
		background-position: 200% 0;
	}
	100% {
		background-position: 0 0;
	}
}

.emblem:after {
	filter: blur(10px);
}

.egg{
  position: absolute;
  top: 100px;
  left: 100px;
 
  margin: 8px;

  width: 5%;
  max-width: 50%;

  display: block;


  animation-name: eggAni;
  animation-duration: 1s;
}

.egg:hover{
  cursor: pointer;
}

.egg:focus{
  cursor: pointer;
}

@keyframes eggAni{
  from{
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}



.wish11Btn{
  position: absolute;
  transform: translate(-50%, -50%);
  height: 35px;
  width: 35px;
  border: none;
  border-radius: 50%;
  outline: 0;
  z-index: -999;
 
  /* background: linear-gradient(to right, #ee0979, #ff6a00); */
  background: radial-gradient( circle farthest-corner at 10% 20%,  rgba(222,168,248,1) 0%, rgba(168,222,258,1) 21.9%, rgba(189,250,205,1) 35.6%,
   rgba(243,250,189,1) 53.9%, rgba(250,227,189,1) 66.8%, rgba(248,172,171,1) 95%, rgba(254,170,212,1) 99.9% );  
}

.traveler,
.darkness,
.rain,
.throne{
    overflow: hidden;
    position: absolute;
    top:0;
    z-index: -999;
}

.ghost_wrapper{
  position:absolute;
  top: 25%;
  left:50%;
  transform: translateX(-50%);
  width: calc(135px * var(--pixel-size));
  height: calc(191px * var(--pixel-size));
  overflow: hidden;
}

.rain{
  filter: blur(1px);
  opacity: .8;
}

.littleLight{
  overflow: hidden;
}

.ball {
  position: absolute;
  border-radius: 100%;
  opacity: 0.7;
  filter: blur(2px) saturate(2);
}

.ghostDarkness{
  position:relative;
  width: calc(1048px * var(--pixel-size));
  overflow: hidden;

  animation: ghostRestricted 1s steps(8);
}

@keyframes ghostRestricted{
  from{
    transform: translate3d(0,0,0);
    opacity: 0;
  }
  to{
    transform: translate3d(-100%,0,0);
    opacity: 1;
  }
}

.caption{
  position: relative;
  display: block;
  text-align: center;
  color: whitesmoke;
  font-size: 1.3rem;
  font-family: serif;
  z-index: 2;
  text-shadow: 1px 1px #111;

  animation: captionAni 1.5s;
}

@keyframes captionAni{
  from{
    top: 5%;
    opacity: 0;
  }
  to{
    top:0;
    opacity: 1;
  }
}

.loot_wrapper{
  position:absolute;
  right:5%;
  width: calc(180px * var(--pixel-size));
  height: calc(180px * var(--pixel-size));
  overflow: hidden;

  animation: lootDrop .8s steps(7);
}

@keyframes lootDrop{
  from{
    top: 0;
  }
  to{
    top:15%;
  }
}

.loot{
  position:absolute;
  top:30.5%;
  right:30.2%;
  height: calc(96px*var(--item-size));
  height: calc(96px*var(--item-size));
  border: 2px solid whitesmoke;
  z-index: 1;
}

.lootitem{
  position:relative;
  width: calc(2896px * var(--pixel-size));
  animation: lootAnimation .8s steps(16);
}

@keyframes lootAnimation{
  0%{
    transform: translateX(0px);
  }
  100%{
    transform: translateX(-100%);
  }
}

#drop,
.hide,
.dropShip,
#eggLoot{
  display: none;
}

#afterDrop,
.show,
.getShip,
#eggLootDrop{
  display: block;
}

.darkness{ 
  animation: darknessAni 3s;
}

@keyframes darknessAni{
 from{
   transform:scale(3);
   top:-30%;
   opacity: 0;
 }
 to{
  transform:scale(1);
  top:0;
  opacity: 1;
 }
}

.lore{
  display: block;
  width: 55%;
  margin: 5% auto;

  background-color: rgba(0, 0, 0, .5);
  box-shadow: 0px 0px 40px black;
}

.lore p,
.lore h3{
  color: whitesmoke;
  text-align: center;
 
  font-size: 2rem;
  font-family: serif;
  font-style: italic;
  padding: 6px;
}

.lore h3{
  font-size: 2.3rem;
  text-decoration: underline;
}

/* /////////////// WISH CHEAT SHEET /////////////// */

.navWrapper{
  display: block;
}


.wishNavClosed{
  height: 100%;
  width: 0;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #5f1047;
  overflow-x: hidden;
  transition: .5s;
  padding-top: 60px;
  border-right: 4px gold solid;

  box-shadow: 0 0 4px 2px #111;
}

.wishNavOpen{
  height: 100%;
  width: 20%;
  position: fixed;
  top: 0;
  left: 0;
  float: left;
  background-color: #5f1047;
  border-right: 4px gold solid;
  overflow-x: hidden;
  transition: .5s;
  padding-top: 60px;

  box-shadow: 0 0 4px 2px #111;
}

.wishNum{
  text-align: center;
  font-size: 2rem;
  font-style: bold;
  color: whitesmoke;
  font-family:serif;
  padding: 4px;
}

.wishImg{
  display: block;
  width: 90%;
}

li{
  padding: 8px 8px 8px 32px;
  display: block;
  transition: .3s;
}

.navBtn{
  position: absolute;
  top:0;
  left:1%;
  padding: 4px;
  z-index: 1;
  font-size: 2rem;
  text-align: center;
  color: whitesmoke;
  background: none;
  border: none;
}

.navBtn:hover{
  cursor: pointer;
}

hr{
  margin: 1rem;
}




